<template>
	<div class="aboutme_div">
		<div class="aboutuser_info">
			<img :src="imgsrc" alt="" style="width:32px;height:32px">
			<span class="aboutename">{{user_name}}</span>
		</div>
		<div class="aboutauth">
			<span class="aboutename">{{$t('user.authorization')}}:</span>
			<span class="aboutename">{{authorize}}</span>
		</div>
		<div class="aboutauth">
			<span class="aboutename">Version</span>
			<span class="aboutename">1.0.2</span>
		</div>
		<div class="aboutout">
				<el-button type="primary" @click="loginOut" >{{$t('common.safeLogout')}}</el-button>
			</div>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				imgsrc:require("../../../assets/images/aboutme_active.png"),
				user_name:localStorage.getItem('user_name'),
				user_group_power:localStorage.getItem('user_group_power')
			}
		},
		computed:{
			authorize:function(){
				if (this.user_group_power == 0) {
					return this.$t('user.sup_user')
				} else if(this.user_group_power == 1) {
					return this.$t('user.man_user')
				}else {
					return this.$t('user.op_user')
				}
			}
		},
		methods:{
			loginOut(){
				localStorage.removeItem('user_name');
        localStorage.removeItem('user_group_power');
        localStorage.removeItem('user_id');
        this.$router.push('/pdalogin')
			}
		}
	}
</script>
<style>
	.aboutuser_info{
		text-align: left;
		display: flex;
		height: 32px;
		line-height: 32px;
		padding:20px;
		background: white;
	}
	.aboutme_info img{
		width:32px;
		height: 32px;
	}
	.aboutename{
		padding-left: 10px;
		font-size: 0.4rem;
		font-weight:500;
	}
	.aboutauth{
		background: white;
		margin-top:10px;
		height: 1.2rem;
		line-height: 1.2rem;
		text-align: left;
		padding-left: 10px;
	}
	.aboutout{
		padding:0 20px;
		margin-top: 20px;
	}
	.aboutout .el-button{
		width:100%;
	}
</style>


